<template>
  <!--  +  viewport  -->
  <section
    ref="stage"
    class="stage fixed inset-0 bg-gray-900 overflow-hidden w-[100dvw] h-[100dvh]"
    style="width:100vw;width:100dvw;height:100vh;height:100dvh"
  >
    <!--  Canvas  -->
    <canvas ref="bgCanvas"    class="bg_canvas absolute inset-0 block w-full h-full z-[5]" />
    <canvas ref="maskCanvas"  class="mask_canvas absolute inset-0 block w-full h-full z-[10]" />
    
    <!--  (iOS  MOV) -->
    <div class="absolute inset-0 pointer-events-none z-[10]">
      <video
        ref="moonVideo"
        v-show="isIOSDevice && ['moon_video','custom_moon_bg','person_overlay'].includes(backgroundType)"
        class="w-full h-full object-contain force-composite"
        autoplay
        loop
        muted
        playsinline
        webkit-playsinline
        style="pointer-events:none; background:transparent;"
      ></video>
    </div>
    
    <canvas ref="outputCanvas"class="output_canvas absolute inset-0 block w-full h-full z-[100]" />

    <!--  -->
    <button v-show="showControls" @click="$emit('capture-photo')" 
            class="absolute bottom-12 left-1/2 -translate-x-1/2 z-[110] w-20 h-20 flex items-center justify-center">
      <img :src="basePath + 'shutter.png'" alt="" class="w-26 h-26" />
    </button>

    <!--  -->
    <div v-show="showControls && showHintText"
         class="absolute bottom-4 left-1/2 -translate-x-1/2 z-[110] text-white text-sm text-center transition-opacity duration-500"
         :class="{ 'opacity-0': !hintVisible }">
      
    </div>

    <!--  -->
    <div v-show="showControls" class="absolute right-4 bottom-8 z-[110] flex flex-col gap-2">
      <button v-show="showShareButton" @click="$emit('share-video')"
              class="px-3 py-2 rounded-xl border border-app-button-border bg-app-button text-app-fg hover:bg-opacity-80 transition-all text-sm">
         
      </button>
      <button v-show="showDownloadButton" @click="$emit('download-video')"
              class="px-3 py-2 rounded-xl border border-app-button-border bg-app-button text-app-fg hover:bg-opacity-80 transition-all text-sm">
         
      </button>
    </div>
  </section>
</template>

<script>
import { ref, computed, onMounted, watch, toRefs } from 'vue'

export default {
  name: 'CameraStage',
  props: {
    backgroundType: {
      type: String,
      default: ''
    },
    isIOSDevice: {
      type: Boolean,
      default: false
    },
    showControls: {
      type: Boolean,
      default: true
    }
  },
  emits: ['capture-photo', 'share-video', 'download-video'],
  setup(props) {
    const { showControls } = toRefs(props)
    
    const stage = ref(null)
    const bgCanvas = ref(null)
    const maskCanvas = ref(null)
    const outputCanvas = ref(null)
    const moonVideo = ref(null)
    const showShareButton = ref(false)
    const showDownloadButton = ref(false)
    
    // 
    const showHintText = ref(true)
    const hintVisible = ref(true)

    // 
    const basePath = computed(() => {
      return import.meta.env.BASE_URL || '/'
    })

    // 
    onMounted(() => {
      setTimeout(() => {
        hintVisible.value = false
        // 
        setTimeout(() => {
          showHintText.value = false
        }, 500) //  CSS transition-opacity duration-500
      }, 10000) // 10
      
      //  src
      if (moonVideo.value) {
        const basePath = import.meta.env.BASE_URL || '/'
        moonVideo.value.src = basePath + 'moon_alpha_ios.mov'
        console.log(' CameraStage moonVideo :', {
          src: moonVideo.value.src,
          element: moonVideo.value,
          isVisible: getComputedStyle(moonVideo.value).display !== 'none'
        })
      }
      
    })

    return {
      stage,
      bgCanvas,
      maskCanvas,
      outputCanvas,
      moonVideo,
      showShareButton,
      showDownloadButton,
      showHintText,
      hintVisible,
      basePath,
      backgroundType: props.backgroundType,
      isIOSDevice: props.isIOSDevice,
      showControls
    }
  }
}
</script>