<template>
  <!--  -  CameraStage  -->
  <section class="fixed inset-0 bg-gray-900 overflow-hidden w-full h-full z-50">
    <!--  Canvas -->
    <canvas ref="bgCanvas"   class="bg_canvas absolute inset-0 block w-full h-full z-[5]" />
    <canvas ref="maskCanvas" class="mask_canvas absolute inset-0 block w-full h-full z-[10]" />

    <!--  MOV  -->
    <div class="absolute inset-0 pointer-events-none z-[15]" :style="contentAreaStyle">
      <video
        ref="moonVideoEl"
        class="w-full h-full object-contain force-composite"
        :src="basePath + 'moon_alpha_ios.mov'"
        autoplay
        loop
        muted
        playsinline
        webkit-playsinline
        style="pointer-events:none; background:transparent; transform:translateZ(0); filter:opacity(1); backface-visibility:hidden;"
      >
        <source :src="basePath + 'moon_alpha_ios.mov'" type="video/quicktime">
        <source :src="basePath + 'moon.mp4'" type="video/mp4">
        <source :src="basePath + 'moon.webm'" type="video/webm">
      </video>
    </div>

    <!--  -->
    <canvas ref="outputCanvas" class="output_canvas absolute inset-0 block w-full h-full z-[20]" />

    <!-- PNG  -->
    <div class="absolute inset-0 pointer-events-none z-[30]">
      <img 
        v-if="currentBackground.frame"
        :src="basePath + currentBackground.frame"
        ref="frameImgEl" 
        alt=""
        class="w-full h-full object-cover"
        style="pointer-events: none;"
        @error="handleFrameError"
        @load="handleFrameLoad"
      />
    </div>

    <!--  -->
    <div class="absolute inset-0 pointer-events-none z-[35]">
      <!--  -->
      <div class="absolute text-white font-mono text-lg tracking-wider" :style="dateTextStyle">
        {{ currentDate }}
      </div>
      
      <!--  -->
      <div class="absolute text-white font-mono text-lg tracking-wider" :style="nameTextStyle">
        {{ userName || 'Guest' }}
      </div>
    </div>

    <!--  -->
    <button
      @click="switchBackground"
      class="absolute top-6 right-6 w-12 h-12 bg-black bg-opacity-60 rounded-full flex items-center justify-center text-white text-xl hover:bg-opacity-80 transition-all z-30"
    >
      
    </button>
    
    <!--  -->
    <div v-if="isProcessing" class="absolute inset-0 flex items-center justify-center bg-black bg-opacity-50 z-30">
      <div class="text-white text-center">
        <div class="animate-spin rounded-full h-12 w-12 border-b-2 border-white mx-auto mb-4"></div>
        <p class="text-lg">...</p>
      </div>
    </div>
    
    <!--  -->
    <div class="absolute bottom-6 left-6 right-6 flex justify-between items-center z-30">
      <button 
        @click="$emit('close')"
        class="w-12 h-12 bg-black bg-opacity-60 rounded-full flex items-center justify-center text-white hover:bg-opacity-80 transition-all"
      >
        
      </button>
      
      <div class="flex gap-4">
        <button 
          @click="downloadPhoto"
          class="w-12 h-12 bg-black bg-opacity-60 rounded-full flex items-center justify-center text-white hover:bg-opacity-80 transition-all"
        >
          
        </button>
        
        <button 
          @click="sharePhoto"
          class="w-12 h-12 bg-black bg-opacity-60 rounded-full flex items-center justify-center text-white hover:bg-opacity-80 transition-all"
        >
          
        </button>
      </div>
    </div>

    <!--  -->
    <div class="hidden">
      <video ref="videoEl" class="input_video" playsinline />
      <video
        ref="bgVideoEl"
        class="bg_video"
        playsinline
        muted
        loop
        preload="auto"
        src="https://interactive-examples.mdn.mozilla.net/media/cc0-videos/flower.mp4"
      />
      <video
        ref="bgMoonVideoEl"
        class="bg_video"
        muted
        loop
        preload="auto"
        playsinline
        style="display: none;"
      >
      </video>
    </div>
  </section>
</template>

<script>
import { ref, computed, onMounted, onBeforeUnmount, nextTick } from 'vue'

export default {
  name: 'PhotoPreview',
  props: {
    originalImageData: {
      type: [String, Object],
      required: true
    },
    availableBackgrounds: {
      type: Array,
      required: true
    },
    selectedBackground: {
      type: String,
      required: true
    },
    userName: {
      type: String,
      default: ''
    }
  },
  emits: ['close', 'background-change'],
  setup(props, { emit }) {
    // Canvas refs -  CameraStage
    const bgCanvas = ref(null)
    const maskCanvas = ref(null)
    const outputCanvas = ref(null)
    const moonVideoEl = ref(null)
    
    //  video 
    const videoEl = ref(null)
    const bgVideoEl = ref(null)
    const bgMoonVideoEl = ref(null)
    
    const isProcessing = ref(false)
    const currentBackgroundIndex = ref(0)

    // 
    const basePath = computed(() => {
      return import.meta.env.BASE_URL || '/'
    })

    // 
    const currentBackground = computed(() => {
      return props.availableBackgrounds[currentBackgroundIndex.value] || props.availableBackgrounds[0]
    })

    const MOON_OFFSET_PCT = 0.06

    const DATE_X_IN_FRAME  = 0.182; // date  x
    const DATE_Y_IN_FRAME  = 0.695; // date  y
    const NAME_X_IN_FRAME  = 0.53; // name  x
    const NAME_Y_IN_FRAME  = 0.695; // name  y
    const TEXT_SIZE_PX     = 18;

    // === reactive  window.* ===
    const viewport = ref({ w: 0, h: 0 });
    const updateViewport = () => {
      viewport.value.w = window.innerWidth || 0;
      viewport.value.h = window.innerHeight || 0;
    };
    onMounted(() => {
      updateViewport();
      window.addEventListener('resize', updateViewport);
    });
    onBeforeUnmount(() => {
      window.removeEventListener('resize', updateViewport);
    });

    const frameImgEl = ref(null);
    const frameNatural = ref({ w: 0, h: 0 });
    const handleFrameLoad = (e) => {
      const img = e?.target || frameImgEl.value;
      if (!img) return;
      frameNatural.value = { w: img.naturalWidth || 0, h: img.naturalHeight || 0 };
      //  viewport  onload  resize 
      updateViewport();
    };

    //  YYYY.MM.DD
    const currentDate = computed(() => {
      const now = new Date()
      const year = now.getFullYear()
      const month = String(now.getMonth() + 1).padStart(2, '0')
      const day = String(now.getDate()).padStart(2, '0')
      return `${year}.${month}.${day}`
    })

    //  contentArea + 
    const contentAreaStyle = computed(() => {
      const area = currentBackground.value?.contentArea;
      if (!area) return {};
      const moonScale = currentBackground.value.moonScale || 1.0;
      const W = viewport.value.w;
      const H = viewport.value.h;

      const baseW = area.width  * W;
      const baseH = area.height * H;
      const baseL = area.x * W;
      const baseT = area.y * H;

      const scaledW = baseW * moonScale;
      const scaledH = baseH * moonScale;
      const scaledL = baseL - (scaledW - baseW) / 2;
      const scaledT = baseT - (scaledH - baseH) / 2;

      return {
        left:   `${scaledL}px`,
        top:    `${scaledT + H * MOON_OFFSET_PCT}px`,
        width:  `${scaledW}px`,
        height: `${scaledH}px`,
      };
    });

    // / PNG 
    const dateTextStyle = computed(() => {
      const W = viewport.value.w, H = viewport.value.h;
      const fw = frameNatural.value.w, fh = frameNatural.value.h;
      const rect = coverRect(W, H, fw, fh);
      const x = rect.x + rect.w * DATE_X_IN_FRAME;
      const y = rect.y + rect.h * DATE_Y_IN_FRAME;
      return {
        position: 'absolute',
        top:  0,
        left: 0,
        transform: `translate(${x}px, ${y}px)`,
        fontSize: `${TEXT_SIZE_PX}px`,
        whiteSpace: 'nowrap',
        textShadow: '0 1px 2px rgba(0,0,0,.6)',
      };
    });

    const nameTextStyle = computed(() => {
      const W = viewport.value.w, H = viewport.value.h;
      const fw = frameNatural.value.w, fh = frameNatural.value.h;
      const rect = coverRect(W, H, fw, fh);
      const x = rect.x + rect.w * NAME_X_IN_FRAME;
      const y = rect.y + rect.h * NAME_Y_IN_FRAME;
      return {
        position: 'absolute',
        top:  0,
        left: 0,
        transform: `translate(${x}px, ${y}px)`,
        fontSize: `${TEXT_SIZE_PX}px`,
        whiteSpace: 'nowrap',
        textShadow: '0 1px 2px rgba(0,0,0,.6)',
      };
    });
    // 
    const handleFrameError = (event) => {
      console.warn(':', event.target.src)
    }

    // 
    const switchBackground = async () => {
      currentBackgroundIndex.value = (currentBackgroundIndex.value + 1) % props.availableBackgrounds.length
      emit('background-change', currentBackground.value.id)
      
      // 
      await renderPreview()
    }

    // 
    const initPreview = async () => {
      if (!bgCanvas.value || !outputCanvas.value || !moonVideoEl.value) {
        console.error('Canvas ')
        return
      }

      try {
        isProcessing.value = true

        //  +  + 
        await renderPreview()
        
        // 
        moonVideoEl.value.play()

      } catch (error) {
        console.error(':', error)
      } finally {
        isProcessing.value = false
      }
    }
    
    // 
    const renderPreview = async () => {
      const bgCtx = bgCanvas.value.getContext('2d')
      const outCtx = outputCanvas.value.getContext('2d')
      
      // 
      const viewportWidth = window.innerWidth
      const viewportHeight = window.innerHeight
      
      bgCanvas.value.width = outputCanvas.value.width = viewportWidth
      bgCanvas.value.height = outputCanvas.value.height = viewportHeight
      
      console.log(' Canvas :', viewportWidth, 'x', viewportHeight)
      
      // 1  bgCanvasz-[5] outputCanvas
      if (currentBackground.value?.src) {
        try {
          const bgImg = new Image()
          bgImg.src = basePath.value + currentBackground.value.src
          await new Promise((resolve, reject) => {
            bgImg.onload = resolve
            bgImg.onerror = reject
          })
          console.log(' :', currentBackground.value.src)
          bgCtx.drawImage(bgImg, 0, 0, viewportWidth, viewportHeight)
        } catch (error) {
          console.error(' :', error)
          bgCtx.fillStyle = '#1a1a1a'
          bgCtx.fillRect(0, 0, viewportWidth, viewportHeight)
        }
      } else {
        bgCtx.fillStyle = '#1a1a1a'
        bgCtx.fillRect(0, 0, viewportWidth, viewportHeight)
      }

      // 2  outputCanvas
      outCtx.clearRect(0, 0, viewportWidth, viewportHeight)

      // 3  DOM z-[15] canvas

      // 4  outputCanvasz-[20]
      let imageData = props.originalImageData
      if (typeof imageData === 'object' && imageData.segmentedImageData) {
        imageData = imageData.segmentedImageData  // 
        console.log(' ')
      } else if (typeof imageData === 'object' && imageData.originalImageData) {
        imageData = imageData.originalImageData   // 
        console.log(' ')
      } else if (typeof imageData === 'string') {
        console.log(' ')
      }
      
      if (imageData) {
        try {
          const personImg = new Image()
          personImg.src = imageData
          await new Promise((resolve, reject) => {
            personImg.onload = resolve
            personImg.onerror = reject
          })
          
          const area = currentBackground.value.contentArea || { x: 0, y: 0, width: 1, height: 1 }
          const contentX = area.x * viewportWidth
          const contentY = area.y * viewportHeight  
          const contentWidth = area.width * viewportWidth
          const contentHeight = area.height * viewportHeight
          
          const imgWidth = personImg.naturalWidth
          const imgHeight = personImg.naturalHeight
          const imgAspect = imgWidth / imgHeight
          const contentAspect = contentWidth / contentHeight
          
          let drawWidth, drawHeight, drawX, drawY
          if (imgAspect > contentAspect) {
            drawWidth = contentWidth
            drawHeight = drawWidth / imgAspect
            drawX = contentX
            drawY = contentY + (contentHeight - drawHeight) / 2
          } else {
            drawHeight = contentHeight
            drawWidth = drawHeight * imgAspect
            drawX = contentX + (contentWidth - drawWidth) / 2
            drawY = contentY
          }
          
          console.log(' :', { 
            contentArea: { contentX, contentY, contentWidth, contentHeight },
            draw: { drawX, drawY, drawWidth, drawHeight },
            scale: drawWidth / imgWidth,
            outputCanvasZIndex: getComputedStyle(outputCanvas.value).zIndex
          })
          
          outCtx.globalCompositeOperation = 'source-over'
          outCtx.drawImage(personImg, drawX, drawY, drawWidth, drawHeight)
          console.log(' PhotoPreview  outputCanvas')
        } catch (error) {
          console.error(' :', error)
        }
      }
    }

    //  5 
    const generatePreviewVideo = async () => {
      isProcessing.value = true;
      try {
        // 1  = viewport DPR 
        const cssW = Math.round(window.innerWidth);
        const cssH = Math.round(window.innerHeight);
        const dpr = Math.min(window.devicePixelRatio || 1, 2); // 

        // 2  canvas 
        const canvas = document.createElement('canvas');
        canvas.width = Math.round(cssW * dpr);
        canvas.height = Math.round(cssH * dpr);
        //  scale CSS 
        const ctx = canvas.getContext('2d');
        ctx.scale(dpr, dpr);

        const stream = canvas.captureStream(30); // 30fps
        const mime = pickMimeType([
          'video/mp4;codecs=h264',  // Safari / iOS
          'video/mp4',
          'video/webm;codecs=vp9',
          'video/webm;codecs=vp8',
          'video/webm'
        ]);
        if (!mime) throw new Error(' MediaRecorder ');

        const chunks = [];
        const rec = new MediaRecorder(stream, {
          mimeType: mime,
          videoBitsPerSecond: 6_000_000
        });
        rec.ondataavailable = e => e.data && e.data.size && chunks.push(e.data);
        const done = new Promise(resolve => {
          rec.onstop = () => {
            const type = mime.includes('mp4') ? 'video/mp4' : 'video/webm';
            const blob = new Blob(chunks, { type });
            const url = URL.createObjectURL(blob);
            resolve({ blob, url, mime: type });
          };
        });

        // 3 
        const bgImg = await loadImageSafe(
          currentBackground.value?.src ? basePath.value + currentBackground.value.src : null
        );

        // /
        let imageData = props.originalImageData;
        if (typeof imageData === 'object' && imageData?.segmentedImageData) {
          imageData = imageData.segmentedImageData;
        } else if (typeof imageData === 'object' && imageData?.originalImageData) {
          imageData = imageData.originalImageData;
        }
        const personImg = await loadImageSafe(imageData);

        //  PNGobject-cover 
        const frameImg = await loadImageSafe(
          currentBackground.value?.frame ? basePath.value + currentBackground.value.frame : null
        );

        // 
        const moonVid = moonVideoEl.value;
        await ensureVideoReady(moonVid);
        try { await moonVid.play(); } catch {} // iOS 

        // 4 
        //  contentArea moonScale 
        const area = currentBackground.value?.contentArea || { x: 0, y: 0, width: 1, height: 1 };
        const personRect = {
          x: area.x * cssW,
          y: area.y * cssH,
          w: area.width * cssW,
          h: area.height * cssH,
        };

        //  contentArea + moonScale contain
        const moonScale = currentBackground.value?.moonScale || 1;
        const baseW = personRect.w;
        const baseH = personRect.h;
        const moonRect = {
          w: baseW * moonScale,
          h: baseH * moonScale,
          x: personRect.x - (baseW * (moonScale - 1)) / 2,
          y: personRect.y - (baseH * (moonScale - 1)) / 2
        };

        moonRect.y += cssH * MOON_OFFSET_PCT;

        // 5  &  5 
        rec.start(100);
        const start = performance.now();

        // 
        drawOneFrame(ctx, cssW, cssH, {
          bgImg, frameImg, personImg, moonVid, personRect, moonRect
        });

        const rafLoop = () => {
          const now = performance.now();
          drawOneFrame(ctx, cssW, cssH, {
            bgImg, frameImg, personImg, moonVid, personRect, moonRect
          });
          if (now - start < 5000) { // 5 
            requestAnimationFrame(rafLoop);
          } else {
            rec.stop();
          }
        };
        requestAnimationFrame(rafLoop);

        // 6 
        const result = await done;
        return result;
      } catch (err) {
        console.error('', err);
        throw err;
      } finally {
        isProcessing.value = false;
      }
    };

    // 
    // MP4/
    const downloadPhoto = async () => {
      try {
        const result = await generatePreviewVideo();
        const link = document.createElement('a');
        link.download = `range_rover_preview_${Date.now()}${result.mime.includes('mp4') ? '.mp4' : '.webm'}`;
        link.href = result.url;
        link.click();
        setTimeout(() => { try { URL.revokeObjectURL(result.url); } catch {} }, 5000);
      } catch (e) {
        console.error('', e);
        alert('');
      }
    };


    // 
    const sharePhoto = async () => {
      try {
        const result = await generatePreviewVideo();
        const fileName = `range_rover_preview${result.mime.includes('mp4') ? '.mp4' : '.webm'}`;
        const file = new File([result.blob], fileName, { type: result.mime });

        if (navigator.share && navigator.canShare?.({ files: [file] })) {
          await navigator.share({
            files: [file],
            title: 'Range Rover ',
            text: ''
          });
        } else {
          // 
          const a = document.createElement('a');
          a.href = result.url;
          a.download = fileName;
          a.click();
          setTimeout(() => { try { URL.revokeObjectURL(result.url); } catch {} }, 5000);
        }
      } catch (e) {
        console.error('', e);
        alert('');
        try { await downloadPhoto(); } catch {}
      }
    };

    //  mime-type
    const pickMimeType = (candidates) => {
      for (const t of candidates) {
        try { if (MediaRecorder.isTypeSupported(t)) return t; } catch {}
      }
      return '';
    };

    //  <video>  canvas
    const ensureVideoReady = (video) => new Promise((resolve) => {
      if (!video) return resolve();
      if (video.readyState >= 2) return resolve(); // HAVE_CURRENT_DATA
      const onReady = () => { cleanup(); resolve(); };
      const cleanup = () => {
        video.removeEventListener('loadeddata', onReady);
        video.removeEventListener('canplay', onReady);
      };
      video.addEventListener('loadeddata', onReady, { once: true });
      video.addEventListener('canplay', onReady, { once: true });
    });

    // src  null
    const loadImageSafe = (src) => new Promise((resolve) => {
      if (!src) return resolve(null);
      const img = new Image();
      img.crossOrigin = 'anonymous';
      img.onload = () => resolve(img);
      img.onerror = () => resolve(null);
      img.src = src;
    });

    //  contain  rect
    const drawContain = (ctx, media, x, y, w, h, naturalW, naturalH) => {
      const mw = naturalW || media.videoWidth || media.naturalWidth || w;
      const mh = naturalH || media.videoHeight || media.naturalHeight || h;
      if (!mw || !mh) return;
      const scale = Math.min(w / mw, h / mh);
      const dw = mw * scale;
      const dh = mh * scale;
      const dx = x + (w - dw) / 2;
      const dy = y + (h - dh) / 2;
      try { ctx.drawImage(media, 0, 0, mw, mh, dx, dy, dw, dh); } catch {}
    };

    //  object-cover  PNG 
    const coverRect = (W, H, mediaW, mediaH) => {
      if (!W || !H || !mediaW || !mediaH) return { x: 0, y: 0, w: W, h: H };
      const scale = Math.max(W / mediaW, H / mediaH);
      const w = mediaW * scale;
      const h = mediaH * scale;
      const x = (W - w) / 2;
      const y = (H - h) / 2;
      return { x, y, w, h };
    };

    //  cover  rect
    const drawCover = (ctx, img, x, y, w, h) => {
      const mw = img.naturalWidth || w;
      const mh = img.naturalHeight || h;
      if (!mw || !mh) return;
      const scale = Math.max(w / mw, h / mh);
      const dw = mw * scale;
      const dh = mh * scale;
      const dx = x + (w - dw) / 2;
      const dy = y + (h - dh) / 2;
      try { ctx.drawImage(img, 0, 0, mw, mh, dx, dy, dw, dh); } catch {}
    };

    //  frame      
    const drawOneFrame = (ctx, W, H, { bgImg, frameImg, personImg, moonVid, personRect, moonRect }) => {
      ctx.clearRect(0, 0, W, H);

      // 
      if (bgImg) {
        try { ctx.drawImage(bgImg, 0, 0, W, H); }
        catch { ctx.fillStyle = '#1a1a1a'; ctx.fillRect(0, 0, W, H); }
      } else {
        ctx.fillStyle = '#1a1a1a';
        ctx.fillRect(0, 0, W, H);
      }

      //  MOV contain  moonRectMOV 
      if (moonVid && moonVid.readyState >= 2) {
        drawContain(ctx, moonVid, moonRect.x, moonRect.y, moonRect.w, moonRect.h);
      }

      //  contain  personRect
      if (personImg) {
        drawContain(ctx, personImg, personRect.x, personRect.y, personRect.w, personRect.h);
      }

      // PNG object-cover 
      if (frameImg) {
        drawCover(ctx, frameImg, 0, 0, W, H);
      }

      //  PNG 
      ctx.font = `${TEXT_SIZE_PX}px monospace`;
      ctx.fillStyle = '#fff';
      ctx.textBaseline = 'top';
      ctx.textAlign = 'left';
      ctx.shadowColor = 'rgba(0,0,0,.6)';
      ctx.shadowBlur = 2;
      ctx.shadowOffsetY = 1;

      // (object-cover  drawCover )
      const rect = coverRect(W, H, frameImg?.naturalWidth || 0, frameImg?.naturalHeight || 0);

      // date
      const dateX = rect.x + rect.w * DATE_X_IN_FRAME;
      const dateY = rect.y + rect.h * DATE_Y_IN_FRAME;
      ctx.fillText(currentDate.value, dateX, dateY);

      // name
      const nameX = rect.x + rect.w * NAME_X_IN_FRAME;
      const nameY = rect.y + rect.h * NAME_Y_IN_FRAME;
      ctx.fillText(props.userName || 'Guest', nameX, nameY);

      // 
      ctx.shadowColor = 'transparent';
      ctx.shadowBlur = 0;
      ctx.shadowOffsetY = 0;
    };

    // 
    onMounted(() => {
      nextTick(() => {
        //  MOV 
        if (bgMoonVideoEl.value) {
          bgMoonVideoEl.value.src = basePath.value + 'moon.mp4'
        }
        
        // 
        setTimeout(() => {
          initPreview()
        }, 100)
      })
    })

    // 
    onBeforeUnmount(() => {
      // 
      if (moonVideoEl.value && !moonVideoEl.value.paused) {
        moonVideoEl.value.pause()
      }
    })

    return {
      bgCanvas,
      maskCanvas,
      outputCanvas,
      moonVideoEl,
      videoEl,
      bgVideoEl,
      bgMoonVideoEl,
      isProcessing,
      currentBackground,
      basePath,
      contentAreaStyle,
      currentDate,
      dateTextStyle,
      nameTextStyle,
      switchBackground,
      downloadPhoto,
      sharePhoto,
      handleFrameError,
      handleFrameLoad,
      frameImgEl
    }
  }
}
</script>